:root {
  box-sizing: border-box;
  font-size: calc(0.8em + 1vw);
}

body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1300px;

  font-family: "Roboto-slab", sans-serif;
  font-weight: normal;
  line-height: 1.2;
  color: $main-text-color;

  word-wrap: break-word;
}

*,
::before,
::after {
  box-sizing: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  cursor: pointer;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

