.header {
  position: relative;
  display: grid;
  padding: 0 $mobilePadding 15px;
  overflow: hidden;

  &::after {
    content: " ";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 2px;
    background-color: $line-color;
  }
}

.header__link {
  display: block;
  padding-top: 1em;
  width: 15rem;
  font-family: "Gilroy", sans-serif;
  font-weight: bold;
  font-size: 3rem;
  line-height: 0.9;
  background: linear-gradient(163deg, $dark-color-letter 15%, $primary-color-dark 15%, $primary-color-dark 30%, $accent-color 30%, $accent-color 45%, $secondary-text-color 45%, $secondary-text-color 60%, $main-accent-color 60%, $main-accent-color 62%, $primary-color 62%, $primary-color 75%, $main-text-color 75%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  &:hover,
  &:focus {
    outline: none;
    background: linear-gradient(163deg, $main-text-color 0%, $main-text-color 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}
