.works__heading {
  order: 1;
  margin: 0;
}

.works__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.works__item:not(:last-child) {
  border-bottom: 1px solid $line-color;
  margin-bottom: 60px;
}

.works__link {
  display: flex;
  flex-direction: row;

  &:hover,
  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

.works__desc {
  line-height: 1.2;
}

.works__shower {
  display: block;
  position: relative;
  width: 2rem;
  height: 1rem;
  margin-top: 5px;

  border: none;
  background: none;

  &:hover,
  &:focus {
    opacity: 0.5;
    outline: none;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 35%;
    left: 5px;
    width: 15px;
    height: 2px;
    background-color: $main-text-color;
    transform: rotate(45deg);
  }

  &::after {
    left: 15px;
    transform: rotate(-45deg);
  }
}

.works__shower--opened {
  transform: rotate(180deg);

  &::before,
  &::after {
    background-color: $main-accent-color;
  }
}
