@font-face {
  font-family: "Roboto-slab";
  src: url("../../fonts/roboto_slab_bold.woff2") format("woff2"), url("../..fonts/roboto_slab_bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto-slab";
  src: url("../../fonts/Roboto-slab-regular.woff2") format("woff2"), url("../../fonts/Roboto-slab-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Gilroy";
  src: url("../../fonts/Gilroy-Black.woff2") format("woff2"), url("../../fonts/Gilroy-Black.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

