:root {
  box-sizing: border-box;
  font-size: calc(0.8em + 1vw); }

body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1300px;
  font-family: "Roboto-slab", sans-serif;
  font-weight: normal;
  line-height: 1.2;
  color: #151c22;
  word-wrap: break-word; }

*,
::before,
::after {
  box-sizing: inherit; }

a {
  color: inherit;
  text-decoration: none; }

a:hover {
  cursor: pointer; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  display: grid;
  grid-template-areas: "header" "main" "footer"; }

.page__header {
  grid-area: header; }

.page__main {
  grid-area: main; }

.page__footer {
  grid-area: footer; }

.page-wrapper {
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
  overflow: hidden; }
  @media (min-width: 768px) {
    .page-wrapper {
      position: relative;
      padding-left: 30px;
      padding-right: 30px; } }
  @media (min-width: 1300px) {
    .page-wrapper {
      padding-left: 100px;
      padding-right: 100px; } }

.button {
  display: inline-block;
  font: inherit;
  text-align: center; }

button:hover {
  cursor: pointer; }

.header {
  position: relative;
  display: grid;
  padding: 0 20px 15px;
  overflow: hidden; }
  .header::after {
    content: " ";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 2px;
    background-color: rgba(28, 37, 46, 0.3); }

.header__link {
  display: block;
  padding-top: 1em;
  width: 15rem;
  font-family: "Gilroy", sans-serif;
  font-weight: bold;
  font-size: 3rem;
  line-height: 0.9;
  background: linear-gradient(163deg, #1c252e 15%, #3d2a2a 15%, #3d2a2a 30%, #302823 30%, #302823 45%, #332a17 45%, #332a17 60%, #710301 60%, #710301 62%, #273325 62%, #273325 75%, #151c22 75%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; }
  .header__link:hover, .header__link:focus {
    outline: none;
    background: linear-gradient(163deg, #151c22 0%, #151c22 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }

.nav {
  font-size: 1.15rem;
  padding-top: 5px; }

.nav__list {
  display: flex;
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  list-style: none; }

.nav__link {
  position: relative;
  font-weight: bold;
  color: #1c252e; }
  .nav__link::before {
    content: "";
    position: absolute;
    top: 1.4rem;
    left: 0;
    width: 0;
    height: 3px;
    background: #710301;
    z-index: -1;
    transition: width;
    transition-duration: 0.1s; }
  .nav__link:hover, .nav__link:focus {
    outline: none; }
    .nav__link:hover::before, .nav__link:focus::before {
      width: 100%; }
  .nav__link:active {
    opacity: 0.6; }

.nav__link--active {
  display: none; }

.main {
  padding: 15px 20px 0; }

.main__heading {
  font-size: 2.2rem;
  margin: 0 0 35px; }

.works__heading {
  order: 1;
  margin: 0; }

.works__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.works__item:not(:last-child) {
  border-bottom: 1px solid rgba(28, 37, 46, 0.3);
  margin-bottom: 60px; }

.works__link {
  display: flex;
  flex-direction: row; }
  .works__link:hover, .works__link:focus {
    outline: none;
    opacity: 0.7; }

.works__desc {
  line-height: 1.2; }

.works__shower {
  display: block;
  position: relative;
  width: 2rem;
  height: 1rem;
  margin-top: 5px;
  border: none;
  background: none; }
  .works__shower:hover, .works__shower:focus {
    opacity: 0.5;
    outline: none; }
  .works__shower::before, .works__shower::after {
    content: "";
    position: absolute;
    top: 35%;
    left: 5px;
    width: 15px;
    height: 2px;
    background-color: #151c22;
    transform: rotate(45deg); }
  .works__shower::after {
    left: 15px;
    transform: rotate(-45deg); }

.works__shower--opened {
  transform: rotate(180deg); }
  .works__shower--opened::before, .works__shower--opened::after {
    background-color: #710301; }

.hidden--js {
  display: none; }

.footer {
  padding: 15px 20px;
  color: white;
  background-color: #1c252e; }
