.nav {
  font-size: 1.15rem;
  padding-top: 5px;
}

.nav__list {
  display: flex;
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav__link {
  position: relative;
  font-weight: bold;
  color: $dark-color-letter;

  &::before {
    content: "";
    position: absolute;
    top: 1.4rem;
    left: 0;
    width: 0;
    height: 3px;
    background: $main-accent-color;

    z-index: -1;

    transition: width;
    transition-duration: 0.1s;
  }

  &:hover,
  &:focus {
    outline: none;

    &::before {
      width: 100%;
    }
  }

  &:active {
    opacity: 0.6;
  }
}

.nav__link--active {
  display: none;
}
